<template>
  <div>
    <el-scrollbar class="scroll-container">
      <el-form ref="Form1" :model="form" :rules="rules" label-width="120px" label-position="right">
        <el-form-item label="所属区域：" prop="region">
          <el-select v-model="form.region" placeholder="请选择" filterable clearable style="width: 178px;" @change="getBoardList(),getBoardSet()">
            <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
        <el-form-item label="是否轮播：" prop="play">
          <el-radio-group v-model="form.play" @input="playChange">
            <el-radio :label="true">看板轮播(多个看板按时间周期轮播)</el-radio>
            <el-radio :label="false">不轮播(只显示单个看板)</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="选择看板：" prop="billboardsIds">
          <template v-if="!form.play">
            <el-radio-group v-model="form.billboardsIds">
              <el-radio v-for="(boardItem, index) in list" :key="index" :border="true" :label="boardItem.id">{{ boardItem.name }}</el-radio>
            </el-radio-group>
          </template>
          <template v-else>
            <el-checkbox-group v-model="form.billboardsIds">
              <el-checkbox v-for="(boardItem, index) in list" :key="index" :border="true" :label="boardItem.id">{{ boardItem.name }}</el-checkbox>
            </el-checkbox-group>
          </template>
        </el-form-item>
        <el-form-item v-if="form.play" label="轮播间隔时长" prop="time">
          <el-select v-model="form.time" placeholder="请选择" filterable clearable style="width: 178px;">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
      </el-form>
    </el-scrollbar>
    <div style="text-align: right;">
      <el-button type="primary" @click="handleSave">保 存</el-button>
      <el-button @click="handleCancel">取 消</el-button>
    </div>
  </div>
</template>

<script>
import { getBoardListApi, saveBoardSetApi, getBoardSetApi } from '@/apis/modules/analysis/board'

const INIT_FORM = () => ({
  region: 1, // 所属区域
  play: false, // 是否轮播
  billboardsIds: undefined, // 选择看板
  time: '' // 轮播时长
})

export default {
  data() {
    return {
      form: INIT_FORM(),
      options1: [
        { label: '科技园', value: 1 },
        { label: '中院', value: 2 }
      ],
      options: [
        { label: '10秒', value: 10 },
        { label: '20秒', value: 20 },
        { label: '30秒', value: 30 },
        { label: '1分钟', value: 60 },
        { label: '10钟', value: 600 }
      ],
      list: [],
      rules: {
        region: [
          { validator: (rule, value, callback) => {
            if (!value) {
              callback(new Error('请选择所属区域'))
            } else {
              callback()
            }
          }, trigger: 'change' }
        ],
        billboardsIds: [
          { validator: (rule, value, callback) => {
            if (this.form.play) {
              if (!value.length) {
                callback(new Error('请至少选择一个看板'))
              } else {
                callback()
              }
            } else {
              if (!value) {
                callback(new Error('请选择看板'))
              } else {
                callback()
              }
            }
          }, trigger: 'change' }
        ],
        time: [
          { validator: (rule, value, callback) => {
            if (!value) {
              callback(new Error('请选择轮播间隔时长'))
            } else {
              callback()
            }
          }, trigger: 'change' }
        ]
      }
    }
  },
  mounted() {
    this.getBoardList()
    this.getBoardSet()
  },
  methods: {
    getBoardList() {
      const params = {
        region: this.form.region
      }
      return new Promise((resolve, reject) => {
        getBoardListApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.list = [...data]
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    getBoardSet() {
      const params = {
        region: this.form.region
      }
      return new Promise((resolve, reject) => {
        getBoardSetApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.form = { ...data }
              this.form.play ? this.$set(this.form, 'billboardsIds', data.billboards.map(item => item.id)) : this.$set(this.form, 'billboardsIds', data.billboards.map(item => item.id)[0])
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleCancel() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({
        name: 'KanbanConfig'
      })
    },
    handleSave() {
      Promise.all([
        this.$refs['Form1'].validate()
      ])
        .then(() => {
          // 正常操作
          const params = {
            set: {
              id: this.form.id,
              play: this.form.play, // 是否轮播
              billboardsIds: this.form.play ? this.form.billboardsIds.join() : this.form.billboardsIds, // 选择看板
              time: this.form.time // 轮播时长
            }
          }
          saveBoardSetApi(params).then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '保存成功'
              })
              this.$store.dispatch('tagsView/delView', this.$route)
              this.$router.push({
                name: 'KanbanConfig'
              })
            }
          }).catch(err => {
            console.log('err', err)
          })
        })
        .catch(() => {
          // 错误操作
          console.log('error submit!!')
          return false
        })
    },
    resetForm() {
      this.$refs['Form1'].resetFields()
    },
    playChange(bool) {
      if (bool) {
        this.form.billboardsIds = []
        this.form.time = ''
      } else {
        this.form.billboardsIds = ''
        this.form.time = ''
      }
      this.$refs.Form1.clearValidate(['billboardsIds', 'time'])
    }
  }
}
</script>

<style scoped lang="scss">
.scroll-container {
    height: calc(100vh - 150px);
}
::v-deep .el-scrollbar {
    .el-scrollbar__wrap {
        overflow: hidden;
    }
    .el-scrollbar__bar {
        // height: 0;
        &.is-horizontal {
            height: 0;
        }
        &.is-vertical {
            width: 0;
        }
    }
}
</style>

